<template>
  <div class="modal-outer">    
    <div class="modal">
      <div class="modal-backdrop" @click="closeModal"></div>
      <div class="modal-content" :class="end ? 'end' : 'start'">
        <img class="cancel" src="../assets/images/cancel.svg" @click="closeModal" />
        <h3>{{ country.name }}</h3>
        <p>Capital - {{ country.capital }}</p>
        <p>Continent - {{ country.continent }}</p>
        <p>Region - {{ country.region_wb }}</p>
        <p>Subregion - {{ country.subregion }}</p>
        <p>Full name - {{ country.formal_en }}</p>
        <p>Type - {{ country.type }}</p>
        <p v-if="country.iso_a2 != -99 && country.iso_a3 != -99">ISO 2 - {{ country.iso_a2 }}, ISO 3 - {{ country.iso_a3 }}</p>
        <p>Economy - {{ country.economy | formatString }}</p>
        <p>Income group - {{ country.income_grp | formatString }}</p>
        <p v-if="country.pop_est !== -99">Population - {{ country.pop_est | formatNumber }}</p>
        <p v-if="country.pop_est !== -99 || country.gdp_md_est !== -99">Gross Domestic Product Per Capita - ${{ calcGDP | formatNumber }}</p>
        <router-link to="/" tag="button" class="btn">Home</router-link>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      end: false
    }
  },
  props: {
    country: {
      type: Object
    },
    close: {
      type: Function
    }
  },
  filters: {
    formatString (value) {
      return value.substring(3)
    },
    formatNumber (value) {
      if ((value / 1000000000) > 1) {
        return `${(value / 1000000000).toFixed(2)} b`
      } else if ((value / 1000000) > 1) {
        return `${(value / 1000000).toFixed(2)} m`
      } else if ((value / 1000) > 1) {
        return `${(value / 1000).toFixed(2)} k`
      } else {
        return value.toFixed(2)
      }
    }
  },
  computed: {
    calcGDP () {
      return this.country.gdp_md_est * 1000000 / this.country.pop_est
    }
  },
  methods: {
    closeModal (event) {
      console.log(event)
      this.end = true
      setTimeout(() => {
        this.close()
      }, 600)      
    }
  }
}
</script>
<style scoped>
body {
  overflow: hidden;
}
.modal {
  min-height: 100vh;
  position: absolute;
  right: 0;
  left: 0;
  z-index: 1051;
  outline: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  opacity: 0.5;
  background-color: #000;
}
.modal-content {
  position: relative;
  z-index: 1052;
  width: 500px;
  transform: perspective(300px);
  padding-bottom: 30px;
  background: #fff;
  font-size: 17px;
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
}
.start {
  -webkit-animation: tiltup 0.7s;
  -moz-animation: tiltup 0.7s;
  -ms-animation: tiltup 0.7s;
  animation: tiltup 0.7s;
}
.end {
  -webkit-animation: tiltbottom 0.7s;
  -moz-animation: tiltbottom 0.7s;
  -ms-animation: tiltbottom 0.7s;
  animation: tiltbottom 0.7s;
}
.modal-content::before {
  background: linear-gradient(to right, #9BFFE6 0%, #A3CCFF 100%);
  content: "";
  display: block;
  height: 3px;
  width: 100%;
}
.cancel {
  position: absolute;
  top: 10px;
  right: 7px;
  width: 16px;
  height: 16px;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.cancel:hover {
  transform: scale(1.2);
}
@keyframes tiltup {
  0% { 
    bottom: -20px;
    opacity: 0; 
    transform: rotateX(-90deg);
    border-top: solid 40px; 
  } 
  30% {
    opacity: 0;
  }
  100% {
    bottom: 0;
  }
}
@keyframes tiltbottom {
  0% {
    bottom: 0;
  }
  100% {
    bottom: -200px; 
    opacity: 0; 
    transform: rotateX(-90deg);
    border-top: solid 40px; 
  }
}
</style>


