<template>
  <div>
    <h1 class="brand">Worldaria</h1>
    <div class="start">
      <div class="left">
        <p>In Game mode you have 5 attempts for each country. If you mark right, you will need to guess the capital of that country. Each right country and capital will give you 1 point. After 5 wrong attempts you will lose 1 point.</p>
        <router-link to="/game" tag="button" class="btn">Game</router-link>
      </div>
      <div class="right">
        <p>In Learn mode you have unlimited attempts. After 5 wrong attempts the right country will be accentuated.</p>
        <router-link to="/learn/full" tag="button" class="btn">Learn</router-link>
        <p>Learn countries by continent.</p>
        <router-link to="/learn/continent" tag="button" class="btn">Continents</router-link>
        <p>Learn regions of countries.</p>
        <router-link to="/learn/countries" tag="button" class="btn">Regions</router-link>
        <p>Learn details.</p>
        <router-link to ="/learn/details" tag="button" class="btn">Details</router-link>
      </div>
  </div>
  </div>
</template>
<script>
export default {
  beforeCreate: function() {
    document.body.className = 'start';
  }
}
</script>
<style scoped>

.start {
  min-height: calc(100vh - 70px);
  display: flex;
  color: #fff;
}
.brand {
  height: 70px;
  line-height: 70px;
  margin: 0;
  text-align: center;
  font-family: 'Michroma', sans-serif;
  font-size: 48px;
  text-transform: uppercase;
  letter-spacing: -6px;
  background-color: rgba(0,0,0,0.5);
  color: #fff;
}
.left, .right {
  width: 50%;
  padding: 20px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 1.6;
  font-size: 20px;
}
.buttons {
  margin: 20px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.buttons > button {
  margin-bottom: 10px;
}
.country_select {
  width: 400px;
  max-width: 90%;
  height: 50px;
  padding: 0 20px;
  color: #2c3e50;
  border: 3px solid #6cb2a1;
  border-radius: 5px;
  outline: none;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: bold;
}
.country_select:focus {
  box-shadow: 0 0 5px 2px #6cb2a1;
}
.country_select option {
  font-size: 16px;
  padding: 10px 0;
}
@media screen and (max-width: 650px) {
  .start {
    flex-wrap: wrap;
  }
  .left, .right {
    width: 100%;
    padding: 20px;
    font-size: 18px;
  }
  .brand {
    font-size: 36px;
  }
}
</style>


