<template>
  <div>
    <Loader v-show="showLoader" /> 
    <GameMode v-if="loaded" :mode="'game'" :geojson="geojson" :mapOptions="options" :stopLoader="stopLoader" />
  </div>
</template>
<script>
import GameMode from '../components/GameMode'
import Loader from '../components/Loader'

export default {
  data () {
    return {
      geojson: [],
      options: {
        zoom: 2,
        center: [40, 0],
        maxBounds: [[90, -180], [-60, 225]]
      },
      loaded: false,
      showLoader: true
    }
  },
  created () {
    this.getContinent()
  },
  methods: {
    async getContinent () {
      this.$http.get(`../json/full.json`)
        .then(response => {
          this.geojson = [...response.body.features]
          this.loaded = true
        }, response => {
          console.log(response)
      })
    },
    stopLoader () {
      this.showLoader = false
    }
  },
  components: {
    GameMode,
    Loader
  }
}
</script>


