import Vue from 'vue'
import Router from 'vue-router'
import Start from '../pages/Start'
import Game from '../pages/Game'
import Learn from '../pages/Learn'
import Subject from '../components/Subject'
import ChooseContinent from '../components/ChooseContinent'
import ChooseCountry from '../components/ChooseCountry'
import CountryDetails from '../components/CountryDetails'

Vue.use(Router)
export default new Router({
  mode: 'history',
  base: '/',
  linkActiveClass: 'active',
  routes: [    
    {
      name: 'Start',
      path: '/',
      component: Start   
    },  
    {
      name: 'Game',
      path: '/game',
      component: Game
    },
    {
      name: 'Learn',
      path: '/learn',
      component: Learn,
      redirect: '/learn/full',
      children: [
        {
          path: '/learn/continent',
          component: ChooseContinent,
          name: 'LearnContinent',
          props: { mode: 'learn' }
        },
        {
          path: '/learn/countries',
          component: ChooseCountry,
          name: 'LearnCountry',
          props: { mode: 'learn' }
        },
        {
          path: '/learn/details',
          component: CountryDetails,
          name: 'CountryDetails',
          props: { mode: 'details' }
        },
        {
          path: '/learn/:name',
          component: Subject,
          name: 'Subject',
          props: { mode: 'learn' }
        },    
      ]
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})


